import React from 'react'
import './index.scss'

export const JobItems = ({ jobs }) => (
  <div className="job-items">
    {jobs.map(({ node }) => (
      <div className="job-items__item clickable">
        <a
          key={node.id}
          className="clickable job-items__link"
          href={node.frontmatter.url}  target="_blank"
        >
          {node.frontmatter.title}
        </a>
        <div className="job-items__icon"> &#x276F;</div>
      </div>
    ))}
  </div>
)
