import React from 'react'
import { graphql } from 'gatsby'
import { Head } from '../components/head'
import { HOME_TITLE } from '../constants'
import { Layout } from '../layout'
import { JobItems } from '../components/job-cards'
import { AboutImagesContainer } from '../components/about-images-container'
import './jobs.scss'
import img1 from '../../content/assets/4.png'
import img2 from '../../content/assets/5.png'
import img3 from '../../content/assets/6.png'
import jobsCoverImage from '../../content/assets/jobs-cover.png'
import { CoverImage } from '../components/cover-image'

const Jobs = ({ data, location }) => {
  const { siteMetadata } = data.site
  const jobs = data.jobs.edges

  const JobTextContent = [
    {
      title: 'COMPETITIVE SALARY',
      text: 'This goes without saying, but we\'ll say it anyway. Great work deserves to be rewarded accordingly. We also throw in some additional fringe benefits such as group insurance, meal vouchers, company bonus, etc.',
    },
    {
      title: 'COMPANY CAR',
      text: 'As our clients aren’t always located near a train or bus station, we provide a company car for both work and personal use. As you wouldn’t get far otherwise, we’ll throw in a fuel card as well. ',
    },
    {
      title: 'HOSPITALIZATION INSURANCE',
      text: 'We make sure your costs are covered in case you ever need hospital care, so you have one thing less to worry about. ',
    },
    {
      title: 'IP (INTELLECTUAL PROPERTY) REWARD',
      text: 'This is a net salary optimization, ruled by the government. Basically, you get to keep a larger portion of your salary after taxes. ',
    },
    {
      title: 'PERSONAL DEVELOPMENT',
      text: 'We set up a plan together with you to figure out which skills you\'d like to improve and what role(s) you like to play in the future. We\'ll monitor your progress through regular talks and meetings. ',
    },
    {
      title: 'A GREAT PLACE TO WORK',
      text: 'Our core culture goes back to collaboration, continuous learning and a lot of fun and humor. That\'s why we invest heavily in the growth of our people through numerous training courses and conferences. In addition, there are regular informal team get-togethers where we discuss the latest design & research trends. Through these initiatives, you will learn a lot while getting to know your colleagues over drinks and takeaway food! ',
    },
  ]

  return (
    <Layout location={location}
            title={siteMetadata.title}>
      <Head title={HOME_TITLE}
            keywords={siteMetadata.keywords} />

      <CoverImage image={jobsCoverImage} />

      <div className='jobs'>
        <div className='jobs__intro'>
          <h1 className='jobs__intro--title'><strong>Join</strong> Our team</h1>
          <p>
            We are always looking for design heroes, research wizards, and development virtuosi. So, if you got what it
            takes - get in touch, and we might work together very soon!
          </p>
        </div>
        <div className='section'>
          <JobItems jobs={jobs} />

        </div>
        <div className='jobs__content'>

          <h2>What’s in it for you?</h2>
          {JobTextContent.map((item, i) =>
            <div className='jobs__content--items'>
              <h3><span className='jobs__content--numbers'>0{i + 1}</span>. {item.title}</h3>
              <p>{item.text}</p>
            </div>,
          )}

        </div>
        <div className='jobs__images'>
          <AboutImagesContainer
            img1={img1}
            img2={img2}
            img3={img3} />
        </div>
      </div>
    </Layout>
  )
}

export default Jobs;

export const pageQuery = graphql`
    query {
        site {
            siteMetadata {
                title
            }
        }
        jobs: allMarkdownRemark(filter: {frontmatter: {type: {eq: "job"}}}, sort: {order: ASC, fields: frontmatter___order}) {
            edges {
                node {
                    id
                    frontmatter {
                        title
                        category
                        url
                    }
                }
            }
        }
    }
`
